'use client'

import { differenceInMinutes, format, set } from 'date-fns'
import { useRef } from 'react'
import {
  colorLookup,
  getClosestIndexForDayViewEvents,
} from '../../../utils/helpers'
import { ThemeColorType } from '../../../utils/types'

export interface TimelineBlockEvent {
  eventUid: string
  title: string
  startDate: string
  endDate: string
  themeColor: ThemeColorType
  [key: string]: any
}

interface TimelineBlockProps {
  index: number
  startColIndex?: number
  baseZIndex?: number
  event: TimelineBlockEvent
  isMultiday?: boolean
  onClickTimelineBlock: (payload: {
    eventUid: string
    top: number
    left: number
    height: number
    width: number
  }) => void
}

export default function TimelineBlock({
  index,
  startColIndex = 0,
  baseZIndex = 1,
  event,
  isMultiday = false,
  onClickTimelineBlock,
}: TimelineBlockProps) {
  const timelineBlockRef = useRef<HTMLDivElement | null>(null)

  const getBlockHeight = (evt: TimelineBlockEvent): number => {
    if (!isMultiday) {
      const basePixel = 11
      const [startHour, startMinuteSeg] = getClosestIndexForDayViewEvents(
        new Date(evt.startDate),
      )
      const [endHour, endMinuteSeg] = getClosestIndexForDayViewEvents(
        new Date(evt.endDate),
      )

      if (startHour === endHour && startMinuteSeg === endMinuteSeg) {
        return 22
      }
      const start = set(new Date(evt.startDate), {
        hours: startHour,
        minutes: startMinuteSeg,
      })
      const end = set(new Date(evt.endDate), {
        hours: endHour,
        minutes: endMinuteSeg,
      })
      const diffSlots = Math.floor(differenceInMinutes(end, start) / 15)
      return diffSlots * basePixel + (diffSlots - 1)
    } else {
      return 22
    }
  }

  const blockSubtitle = (blockHeight: number, multi: boolean) => {
    if (!multi) {
      if (blockHeight > 22) {
        const startTime = format(new Date(event.startDate), 'K:mm aaa')
        const endTime = format(new Date(event.endDate), 'K:mm aaa')

        return (
          <div className="pt-0.5">
            {`${startTime} - ${endTime}`}
          </div>
        )
      } else {
        return `, ${format(new Date(event.startDate), 'K aaa')}`
      }
    }
    return null
  }

  const handleOnClickBlock = () => {
    if (!timelineBlockRef.current) return
    const { top, left, height, width } =
      timelineBlockRef.current.getBoundingClientRect()

    onClickTimelineBlock({
      eventUid: event.eventUid,
      top: top - 65,
      left: left - 248,
      height,
      width,
    })
  }

  const heightPx = getBlockHeight(event)

  return (
    <div
      ref={timelineBlockRef}
      style={{ height: `${heightPx}px` }}
      className={` relative-top-px rounded-md text-xs
        overflow-x-hidden
        flex-1
        px-2.5
        pt-0.5
        cursor-pointer
        ${colorLookup[event.themeColor]}
        ${`z-${1 + baseZIndex + index}`}
        ${startColIndex > 0 ? 'border border-white' : ''}
        ${!isMultiday && index > 0 ? 'border-l border-white -ml-2' : ''}
      `}
      onClick={handleOnClickBlock}
    >
      <div className="absolute min-w-0 font-normal truncate">
        {event.title}
        {blockSubtitle(heightPx, isMultiday)}
      </div>
    </div>
  )
}
