'use client'

import { eachDayOfInterval, format, setHours, setMinutes } from 'date-fns'
import { fr } from 'date-fns/locale'
import { useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { setSelectedEvent } from '../../../reducers/calendar/calendarSettingSlice'
import { getDateRange } from '../../../utils/helpers'
import { CalendarViewTypes, CalendarViewType } from '../../../utils/types'
import Timeline from './Timeline'
import TimelineHeader from './TimelineHeader'

interface CalendarSettingState {
  targetDate: string
  calendarViewType: CalendarViewType
}

interface TimelineEvents {
  wholeDayEvents?: any[]
  [key: string]: any
}

interface CalendarState {
  dayViewEvents: TimelineEvents
  weekViewEvents: TimelineEvents
}

interface RootState {
  calendarSetting: CalendarSettingState
  calendar: CalendarState
}

export default function TimelineWrapper() {
  const timelineHeaderRef = useRef<HTMLDivElement | null>(null)
  const timelineRef = useRef<HTMLDivElement | null>(null)
  const dispatch = useDispatch()

  const { targetDate, calendarViewType } = useSelector(
    (state: RootState) => state.calendarSetting,
  )
  const { dayViewEvents, weekViewEvents } = useSelector(
    (state: RootState) => state.calendar,
  )
  const [targetDateRange, setTargetDateRange] = useState<Date[]>([])
  const [timelineMaxHeight, setTimelineMaxHeight] = useState( 'calc(100vh - (65px + 43px))',)

  const onClickTimelineBlock = (payload: any) => {
    dispatch(setSelectedEvent(payload))
  }

  useEffect(() => {
    const getDayList = (
      target: string,
      viewType: CalendarViewType,
    ): Date[] => {
      switch (viewType) {
        case CalendarViewTypes.WEEK_VIEW: {
          const [start, end] = getDateRange(viewType, target)
          return eachDayOfInterval({ start, end })
        }
        default:
          return []
      }
    }

    if (targetDate) {
      setTargetDateRange(getDayList(targetDate, calendarViewType))
    }
  }, [targetDate, calendarViewType])

  useEffect(() => {
    if (!timelineHeaderRef.current) return
    const height =
      timelineHeaderRef.current.getBoundingClientRect().height
    setTimelineMaxHeight(`calc(100vh - (65px + ${height}px))`)
  }, [targetDate, dayViewEvents, weekViewEvents])

  return (
    <div className="flex flex-col w-full">
      <TimelineHeader ref={timelineHeaderRef} dates={targetDateRange} calendarViewType={calendarViewType} onClickTimelineBlock={onClickTimelineBlock}/>
      <div ref={timelineRef} style={{ maxHeight: timelineMaxHeight }} className="flex flex-auto overflow-y-scroll pt-4">
       <div className="min-w-56">
          {Array.from({ length: 14 }).map((_, idx) => {
            const hour = 6 + idx
            const date = setMinutes(setHours(new Date(), hour), 0)
            return (
              <div key={hour} className="flex items-start justify-between h-12 text-gray-500">
                <span className="relative text-right -top-2">{format(date, 'HH:mm', { locale: fr })}</span>
                <div className="relative w-2 h-12 border-t -top-px" />
              </div>
            )
          })}
        </div>

        {calendarViewType === CalendarViewTypes.WEEK_VIEW &&
          Object.keys(weekViewEvents || {}).length > 0 && (
            <Timeline
              calendarViewType={calendarViewType}
              data={weekViewEvents}
              onClickTimelineBlock={onClickTimelineBlock}
            />
          )}
      </div>
    </div>
  )
}
