'use client'

import { format } from 'date-fns'
import { fr } from 'date-fns/locale'
import React, { ForwardedRef, forwardRef } from 'react'
import { useDispatch } from 'react-redux'
import { setTargetDate } from '../../../reducers/calendar/calendarSettingSlice'
import { getUpdatedEventBlocksWithPlacement} from '../../../utils/helpers'
import { CalendarViewTypes, CalendarViewType } from '../../../utils/types'
import TimelineBlock from './TimelineBlock'

interface WholeDayEventBlock {
  isEmpty?: boolean
  duration?: number
  [key: string]: any
}

interface WholeDayEventsDay {
  events: WholeDayEventBlock[]
  incomingRowsMatrix: any
}

type WholeDayEvents = | { 0: WholeDayEventBlock[] } | { [key: string]: WholeDayEventsDay }

type WholeDayEventsInput = WholeDayEvents | any[] | Record<string, any>

interface TimelineHeaderProps {
  dates?: (string | Date)[]
  wholeDayEvents?: WholeDayEventsInput
  calendarViewType: CalendarViewType
  onClickTimelineBlock: (event: any) => void
}

function TimelineHeader({ dates = [], wholeDayEvents = {} as WholeDayEvents, calendarViewType, onClickTimelineBlock,}: TimelineHeaderProps, ref: ForwardedRef<HTMLDivElement>){
  const dispatch = useDispatch()
  const navigateToDay = (date: string | Date) => {
    if (dates.length > 1) {
      dispatch(setTargetDate(new Date(date).toString()))
    }
  }
  const hasMultidayEvents = (
    wholeDayEventsParam: WholeDayEventsInput,
  ): boolean => {
    if (Array.isArray(wholeDayEventsParam)) {
      return wholeDayEventsParam.length > 0
    }
    const whole = wholeDayEventsParam as { [key: string]: WholeDayEventsDay }
    const total = Object.keys(whole).reduce((acc, cur) => {
      acc += whole[cur].events.length
      return acc
    }, 0)
    return total > 0
  }

  const weekViewMultidayEvents = (
    wholeDayEventsWeek: { [key: string]: WholeDayEventsDay },
  ) => {
    const sortedEventsByDay: any[][] = []
    let sundayEvent: any[] | undefined
    if (Object.keys(wholeDayEventsWeek).length > 0) {
      Object.keys(wholeDayEventsWeek).forEach(key => {
        const { events: eventsArr, incomingRowsMatrix } = wholeDayEventsWeek[key]
        const updated = getUpdatedEventBlocksWithPlacement( eventsArr, incomingRowsMatrix,)
        if (Number(key) === 0) {
          sundayEvent = updated
        } else {
          sortedEventsByDay.push(updated)
        }
      })
      if (sundayEvent) {
        sortedEventsByDay.push(sundayEvent)
      }
    }
    return sortedEventsByDay.map((events, i) => (
      <div key={`jour-${i}`} className="flex-1">
        {events.map((event: any, j: number) => {
          if (event.isEmpty) {
            return (
              <div key={`empty-${i}-${j}`} style={{ height: 22 }} className={`relative ${j > 0 ? 'mt-0.5' : ''}`}/>
            )
          }
          return (
            <div
              key={`event-${i}-${j}`}
              style={{
                height: 22,
                width: `calc(100% + (100% * ${event.duration}) - 0.5rem)`,
              }}
              className={`${j > 0 ? 'mt-0.5' : ''}`}
            >
              <div className=" min-w-full">
                <TimelineBlock
                  index={j}
                  baseZIndex={1}
                  event={event}
                  isMultiday
                  onClickTimelineBlock={onClickTimelineBlock}
                />
              </div>
            </div>
          )
        })}
      </div>
    ))
  }
  return (
    <div ref={ref} className="flex flex-col">
      <div className="flex justify-between w-full pl-14">
        {dates.map((date, i) => (
          <div
            key={i}
            className={`
              flex flex-col items-center justify-start
              ${dates.length > 1 ? 'flex-1' : ''}
              ${dates.length === 1 ? 'pl-3' : ''}
            `}
          >
            <span className="flex items-center justify-center h-8 mt-2 font-medium tracking-widest text-gray-600 text-xxs">
              {format(new Date(date), 'EEE', { locale: fr }).toUpperCase()}
            </span>
            <span className={` flex items-center justify-center text-2xl transition tracking-wider text-gray-700 -mt-1 w-10 h-10
                ${
                  dates.length > 1
                    ? 'hover:bg-gray-100 rounded-full cursor-pointer'
                    : ''
                }
              `}
              onClick={() => navigateToDay(date)}
            >
              {format(new Date(date), 'd', { locale: fr })}
            </span>
          </div>
        ))}
      </div>

      <div className="flex">
        <div className="flex items-end justify-start text-gray-500 text-xxs">
          <div className="flex items-end justify-between min-h-0 min-w-56">
            <div className="w-2 border-b" />
          </div>
        </div>
        <div className={` w-full pb-1 border-b border-l${ hasMultidayEvents(wholeDayEvents) &&'mt-3'}`}>
          {calendarViewType === CalendarViewTypes.WEEK_VIEW && (
            <div className="flex justify-between">{weekViewMultidayEvents(wholeDayEvents as { [key: string]: WholeDayEventsDay })}</div>
          )}
        </div>
      </div>
    </div>
  )
}

export default forwardRef<HTMLDivElement, TimelineHeaderProps>( TimelineHeader)
