import { useEffect, useRef } from 'react'

interface DropdownItem {
  leftLabel: string
  rightLabel: string
  onClickHandler: () => void
}

interface DropdownProps {
  data?: DropdownItem[]
  onCloseDropdown: () => void
}

export default function Dropdown({ data = [], onCloseDropdown,}: DropdownProps) {
  const dropdownRef = useRef<HTMLDivElement | null>(null)
  useEffect(() => {
    const checkOutsideClick = (e: MouseEvent) => {
      if (dropdownRef.current) {
        const outsideClick = !dropdownRef.current.contains(e.target as Node)
        if (outsideClick) {
          onCloseDropdown()
        }
      }
    }
    document.addEventListener('click', checkOutsideClick)
    return () => {
      document.removeEventListener('click', checkOutsideClick)
    }
  }, [onCloseDropdown])
  const handleOnClick = (handler: () => void) => {
    handler()
    onCloseDropdown()
  }
  return (
    <div ref={dropdownRef} className="absolute left-0 z-50 py-2 bg-white rounded-md dropdownBoxShadow min-w-168 top-full">
      {data.map((d, i) => (
        <div key={i} onClick={() => handleOnClick(d.onClickHandler)} className="flex items-center justify-between px-4 py-2 transition cursor-pointer hover:bg-gray-100">
          <div className="text-sm">{d.leftLabel}</div>
          <div className="text-sm text-gray-400">{d.rightLabel}</div>
        </div>
      ))}
    </div>
  )
}
