'use client'

import {
  endOfDay,
  format,
  getDate,
  getHours,
  getMinutes,
  getMonth,
  getYear,
  isAfter,
  isBefore,
  set as setDate,
  startOfDay,
} from 'date-fns'
import { fr } from 'date-fns/locale' // <= locale FR
import { useEffect, useRef, useState } from 'react'
import {
  MdAccessTime,
  MdArrowDropDown,
  MdCheckBox,
  MdCheckBoxOutlineBlank,
  MdClose,
  MdColorLens,
  MdLocationOn,
  MdSubject,
} from 'react-icons/md'
import { useDispatch, useSelector } from 'react-redux'
import { v4 as uuid } from 'uuid'
import {
  setTargetDate,
  setCreateEventBasis,
  setIsCreateEventModalOpen,
} from '../../reducers/calendar/calendarSettingSlice'
import { addNewEventToDB } from '../../reducers/dbSlice'
import {
  colorLookup,
  isValidTime,
  repeatTypeStringLookup,
} from '../../utils/helpers'
import {
  EventRepeatTypes,
  EventRepeatType,
  ThemeColorTypes,
  ThemeColorType,
} from '../../utils/types'
import IconButton from '../Common/Button/IconButton'
import Calendar from '../Common/Calendar/Calendar'
import Dropdown from '../Common/Dropdown/Dropdown'

interface User {
  userUid: string
  defaultThemeColor: number
}

interface CreateEventBasis {
  startDate: string
  endDate: string
}

interface CalendarSettingState {
  createEventBasis: CreateEventBasis
}

interface RootState {
  user: User
  calendarSetting: CalendarSettingState
}

interface RepeatTypeItem {
  leftLabel: string
  rightLabel: string
  onClickHandler: () => void
}

interface CreateEventModalProps {
  isEdit?: boolean
}

export default function CreateEventModal({
  isEdit = false,
}: CreateEventModalProps) {
  const createEventModalRef = useRef<HTMLDivElement | null>(null)
  const startDateInputRef = useRef<HTMLInputElement | null>(null)
  const endDateInputRef = useRef<HTMLInputElement | null>(null)

  const [isMouseDown, setIsMouseDown] = useState(false)
  const dispatch = useDispatch()
  const currentUser = useSelector((state: RootState) => state.user)
  const { createEventBasis } = useSelector(
    (state: RootState) => state.calendarSetting,
  )

  const [title, setTitle] = useState('')
  const [location, setLocation] = useState('')
  const [description, setDescription] = useState('')

  const [startDate, setStartDate] = useState<string>(new Date().toString())
  const [startDateInputValue, setStartDateInputValue] = useState('')
  const [showStartDateInput, setShowStartDateInput] = useState(false)

  const [endDate, setEndDate] = useState<string>(new Date().toString())
  const [endDateInputValue, setEndDateInputValue] = useState('')
  const [showEndDateInput, setShowEndDateInput] = useState(false)

  const [isAllDay, setIsAllDay] = useState(false)
  const [repeatType, setRepeatType] = useState<-1 | EventRepeatType>(-1)
  const [themeColor, setThemeColor] = useState<ThemeColorType>(
    ThemeColorTypes.RED,
  )

  const [isRepeatTypeDropdownOpen, setIsRepeatTypeDropdownOpen] =
    useState(false)
  const [isStartDateCalendarOpen, setIsStartDateCalendarOpen] =
    useState(false)
  const [isEndDateCalendarOpen, setIsEndDateCalendarOpen] = useState(false)

  const repeatTypeDropdownData: RepeatTypeItem[] = [
    {
      leftLabel: 'Ne se répète pas',
      rightLabel: '',
      onClickHandler: () => {
        setRepeatType(-1)
        setIsRepeatTypeDropdownOpen(false)
      },
    },
    {
      leftLabel: 'Quotidien',
      rightLabel: '',
      onClickHandler: () => {
        setRepeatType(EventRepeatTypes.DAILY)
        setIsRepeatTypeDropdownOpen(false)
      },
    },
    {
      leftLabel: 'Hebdomadaire',
      rightLabel: '',
      onClickHandler: () => {
        setRepeatType(EventRepeatTypes.WEEKLY)
        setIsRepeatTypeDropdownOpen(false)
      },
    },
    {
      leftLabel: 'Toutes les deux semaines',
      rightLabel: '',
      onClickHandler: () => {
        setRepeatType(EventRepeatTypes.BI_WEEKLY)
        setIsRepeatTypeDropdownOpen(false)
      },
    },
    {
      leftLabel: 'Mensuel',
      rightLabel: '',
      onClickHandler: () => {
        setRepeatType(EventRepeatTypes.MONTHLY)
        setIsRepeatTypeDropdownOpen(false)
      },
    },
    {
      leftLabel: 'Jours de semaine',
      rightLabel: '',
      onClickHandler: () => {
        setRepeatType(EventRepeatTypes.WEEK_DAYS)
        setIsRepeatTypeDropdownOpen(false)
      },
    },
  ]

  const onToggleAllDay = (newVal: boolean) => {
    if (newVal) {
      const updatedStartDate = startOfDay(new Date(startDate))
      const updatedEndDate = endOfDay(new Date(endDate))
      setStartDate(updatedStartDate.toString())
      setEndDate(updatedEndDate.toString())
    } else {
      const now = new Date()
      const curHour = getHours(now)
      const curMin = getMinutes(now)
      setStartDate(
        setDate(new Date(startDate), {
          hours: curHour,
          minutes: curMin,
        }).toString(),
      )
      setEndDate(
        setDate(new Date(endDate), {
          hours: curHour,
          minutes: curMin,
        }).toString(),
      )
    }

    setIsAllDay(newVal)
  }

  const onChangeDate = (target: 'start' | 'end', newDateVal: Date) => {
    const base = new Date(newDateVal)

    const year = getYear(base)
    const month = getMonth(base)
    const date = getDate(base)

    if (target === 'start') {
      const updated = setDate(new Date(startDate), { year, month, date })

      setStartDate(updated.toString())
      dispatch(setTargetDate(updated.toString()))

      if (isBefore(new Date(endDate), updated)) {
        setEndDate(updated.toString())
      }

      setIsStartDateCalendarOpen(false)
    } else {
      const updated = setDate(new Date(endDate), { year, month, date })

      setEndDate(updated.toString())

      if (isBefore(updated, new Date(startDate))) {
        dispatch(setTargetDate(updated.toString()))
        setStartDate(updated.toString())
      }

      setIsEndDateCalendarOpen(false)
    }
  }

  const onBlurInput = (target: 'start' | 'end', value: string) => {
    const [valid, hours, minutes] = isValidTime(value)

    if (target === 'start') {
      if (valid) {
        const newStartDate = setDate(new Date(startDate), {
          hours,
          minutes,
        })

        if (isAfter(newStartDate, new Date(endDate))) {
          const newEndDate = setDate(new Date(endDate), {
            hours,
            minutes,
          })
          setEndDate(newEndDate.toString())
        }

        setStartDate(newStartDate.toString())
      }

      setShowStartDateInput(false)
    } else {
      if (valid) {
        const newEndDate = setDate(new Date(endDate), { hours, minutes })
        if (isBefore(newEndDate, new Date(startDate))) {
          const newStartDate = setDate(new Date(startDate), {
            hours,
            minutes,
          })
          setStartDate(newStartDate.toString())
        }
        setEndDate(newEndDate.toString())
      }
      setShowEndDateInput(false)
    }
  }

  const onSaveEvent = () => {
    let eventGroupUid: string | null = null
    let eventGroupDetail:
      | {
          groupUid: string
          repeatType: EventRepeatType
          repeatChanges: Record<string, unknown>
        }
      | null = null

    if (repeatType > -1) {
      eventGroupUid = uuid()
      eventGroupDetail = {
        groupUid: eventGroupUid,
        repeatType: repeatType as EventRepeatType,
        repeatChanges: {},
      }
    }

    const newEvent = {
      title: title.length > 0 ? title : '(Sans titre)',
      description,
      eventCreatorUid: currentUser.userUid,
      eventGroupUid,
      startDate,
      endDate,
      themeColor,
      location,
      invites: [] as string[],
      isAllDay,
      eventUid: uuid(),
      createdAt: new Date().toString(),
    }

    dispatch(setIsCreateEventModalOpen(false))
    dispatch(
      addNewEventToDB({
        userUid: currentUser.userUid,
        event: newEvent,
        eventGroup: eventGroupDetail,
      }),
    )
  }

  useEffect(() => {
    setStartDate(createEventBasis.startDate.toString())
    setEndDate(createEventBasis.endDate.toString())
  }, [createEventBasis])

  useEffect(() => {
    const onMouseMove = (e: MouseEvent) => {
      if (isMouseDown && createEventModalRef.current) {
        e.preventDefault()
        const deltaX = e.movementX
        const deltaY = e.movementY
        const rect = createEventModalRef.current.getBoundingClientRect()
        createEventModalRef.current.style.left = rect.x + deltaX + 'px'
        createEventModalRef.current.style.top = rect.y + deltaY + 'px'
      }
    }
    document.addEventListener('mousemove', onMouseMove)
    return () => {
      document.removeEventListener('mousemove', onMouseMove)
    }
  }, [isMouseDown])

  useEffect(() => {
    if (currentUser) {
      setThemeColor(currentUser.defaultThemeColor as ThemeColorType)
    }
  }, [currentUser])

  // Inputs heures au format français 24h
  useEffect(() => {
    setStartDateInputValue(format(new Date(startDate), 'HH:mm', { locale: fr }))
  }, [startDate])

  useEffect(() => {
    setEndDateInputValue(format(new Date(endDate), 'HH:mm', { locale: fr }))
  }, [endDate])

  useEffect(() => {
    if (showStartDateInput && startDateInputRef.current) {
      startDateInputRef.current.focus()
    }
    if (showEndDateInput && endDateInputRef.current) {
      endDateInputRef.current.focus()
    }
  }, [showStartDateInput, showEndDateInput])

  return (
    <div
      ref={createEventModalRef}
      className="fixed z-40 flex flex-col bg-white rounded-md h-580px min-w-448 bottom-10 left-10 createEventModalBoxShadow"
    >
      <div
        className={`
          flex items-center justify-between px-3 py-1 rounded-t-md bg-opacity-20 transition cursor-move
          ${colorLookup[themeColor]}
        `}
        onMouseDown={() => setIsMouseDown(true)}
        onMouseUp={() => setIsMouseDown(false)}
      >
        <IconButton
          label="Fermer"
          size="small"
          tooltipLocation="bottom"
          imgComponent={
            <MdClose size="24px" color="rgba(75, 85, 99)" />
          }
          onClickHandler={() => dispatch(setIsCreateEventModalOpen(false))}
        />
      </div>

      <div className="pt-2">
        {/* Titre */}
        <div className="flex py-4 pl-16 pr-5">
          <input
            type="text"
            value={title}
            placeholder="Ajouter un titre"
            onChange={e => setTitle(e.target.value)}
            className="w-full text-2xl antialiased font-normal border-b outline-none"
          />
        </div>

        {/* Bloc date / heure */}
        <div className="flex px-5 py-4">
          <div className="flex items-start justify-center">
            <MdAccessTime size="20px" color="rgba(75, 85, 99)" />
          </div>
          <div className="flex flex-col flex-auto pl-6 text-sm text-gray-500">
            <div className="flex items-center justify-start">
              {/* Start date */}
              <div className="relative">
                <div
                  className="px-1 transition rounded-sm cursor-pointer hover:bg-gray-100"
                  onClick={() => setIsStartDateCalendarOpen(true)}
                >
                  {format(new Date(startDate), 'EEEE d MMMM', { locale: fr })}
                </div>
                {isStartDateCalendarOpen && (
                  <div className="absolute z-50 w-64 bg-white h-min top-full dropdownBoxShadow">
                    <Calendar
                      targetDate={startDate}
                      onClickDate={newDate =>
                        onChangeDate('start', newDate)
                      }
                      isModal
                      onClose={() => setIsStartDateCalendarOpen(false)}
                    />
                  </div>
                )}
              </div>

              {/* Start time */}
              {!isAllDay && (
                <div>
                  {showStartDateInput ? (
                    <input
                      ref={startDateInputRef}
                      type="text"
                      value={startDateInputValue}
                      className="w-20 text-center border rounded-md outline-none"
                      onChange={e =>
                        setStartDateInputValue(e.target.value)
                      }
                      onBlur={e =>
                        onBlurInput('start', e.target.value)
                      }
                    />
                  ) : (
                    <div
                      className="px-1 transition rounded-sm cursor-pointer hover:bg-gray-100"
                      onClick={() => setShowStartDateInput(true)}
                    >
                      {format(new Date(startDate), 'HH:mm', { locale: fr })}
                    </div>
                  )}
                </div>
              )}

              <span className="px-1">-</span>

              {/* End time */}
              {!isAllDay && (
                <div>
                  {showEndDateInput ? (
                    <input
                      ref={endDateInputRef}
                      type="text"
                      value={endDateInputValue}
                      className="w-20 text-center border rounded-md outline-none"
                      onChange={e =>
                        setEndDateInputValue(e.target.value)
                      }
                      onBlur={e => onBlurInput('end', e.target.value)}
                    />
                  ) : (
                    <div
                      className="px-1 transition rounded-sm cursor-pointer hover:bg-gray-100"
                      onClick={() => setShowEndDateInput(true)}
                    >
                      {format(new Date(endDate), 'HH:mm', { locale: fr })}
                    </div>
                  )}
                </div>
              )}

              {/* End date */}
              <div className="relative">
                <div
                  className="px-1 transition rounded-sm cursor-pointer hover:bg-gray-100"
                  onClick={() => setIsEndDateCalendarOpen(true)}
                >
                  {format(new Date(endDate), 'EEEE d MMMM', { locale: fr })}
                </div>
                {isEndDateCalendarOpen && (
                  <div className="absolute z-50 w-64 bg-white h-min top-full dropdownBoxShadow">
                    <Calendar
                      targetDate={endDate}
                      onClickDate={newDate =>
                        onChangeDate('end', newDate)
                      }
                      isModal
                      onClose={() => setIsEndDateCalendarOpen(false)}
                    />
                  </div>
                )}
              </div>
            </div>

            {/* All day */}
            <div className="flex items-center mt-2 -ml-1">
              <div className="mr-1.5">
                <IconButton
                  label="Toute la journée"
                  size="small"
                  tooltipLocation="bottom"
                  imgComponent={
                    isAllDay ? (
                      <MdCheckBox size="24px" color="gray" />
                    ) : (
                      <MdCheckBoxOutlineBlank
                        size="24px"
                        color="gray"
                      />
                    )
                  }
                  onClickHandler={() => onToggleAllDay(!isAllDay)}
                />
              </div>
              <div>Toute la journée</div>
            </div>

            {/* Repeat selector */}
            <div className="relative mt-2">
              <button
                className="flex px-2 py-1 -ml-1 transition rounded-md hover:bg-gray-100"
                onClick={() => setIsRepeatTypeDropdownOpen(true)}
              >
                <span className="mr-0.5">
                  {repeatTypeStringLookup[
                    repeatType as -1 | EventRepeatType
                  ]}
                </span>
                <MdArrowDropDown
                  size="18px"
                  color="rgba(75, 85, 99)"
                />
              </button>
              {isRepeatTypeDropdownOpen && (
                <Dropdown
                  data={repeatTypeDropdownData}
                  onCloseDropdown={() =>
                    setIsRepeatTypeDropdownOpen(false)
                  }
                />
              )}
            </div>
          </div>
        </div>

        {/* Location */}
        <div className="flex items-center px-5 py-4 border-t">
          <div>
            <MdLocationOn size="20px" color="rgba(75, 85, 99)" />
          </div>
          <div className="flex-auto pl-6">
            <input
              type="text"
              value={location}
              placeholder="Lieu"
              onChange={e => setLocation(e.target.value)}
              className="w-full px-2 py-1 text-sm bg-gray-100 border-b outline-none"
            />
          </div>
        </div>

        {/* Description */}
        <div className="flex px-5 py-4 border-t">
          <div>
            <MdSubject size="20px" color="rgba(75, 85, 99)" />
          </div>
          <div className="flex-auto pl-6">
            <textarea
              value={description}
              placeholder="Description"
              onChange={e => setDescription(e.target.value)}
              className="w-full px-2 py-1 text-sm bg-gray-100 border-b outline-none resize-none"
            />
          </div>
        </div>

        {/* Couleur */}
        <div className="flex px-5 py-4 border-t border-b">
          <div>
            <MdColorLens size="20px" color="rgba(75, 85, 99)" />
          </div>
          <div className="flex flex-auto pl-6 space-x-4 text-xs -mt-1.5">
            {Object.keys(ThemeColorTypes).map(key => {
              if (!isNaN(Number(key))) {
                return null
              }
              const colorIndex = (ThemeColorTypes as any)[
                key
              ] as ThemeColorType
              return (
                <div
                  key={key}
                  className={`
                    flex flex-col items-center px-1 py-1.5 transition rounded-md cursor-pointer
                    hover:${colorLookup[colorIndex]}
                    hover:bg-opacity-20
                    ${
                      themeColor === colorIndex
                        ? `${colorLookup[colorIndex]} bg-opacity-20`
                        : ''
                    }
                  `}
                  onClick={() => setThemeColor(colorIndex)}
                >
                  <span className="mb-2 tracking-wide text-gray-500 text-xxs">
                    {key}
                  </span>
                  <div
                    className={`
                      w-4 h-4 rounded-full
                      ${colorLookup[colorIndex]}
                    `}
                  />
                </div>
              )
            })}
          </div>
        </div>
      </div>
      <div className="flex items-center justify-end px-4 py-3">
        <button
          className={`
            px-6 py-2 text-sm text-white bg-opacity-80 transition
            ${colorLookup[themeColor]} rounded-md
          `}
          onClick={onSaveEvent}
        >
          Enregistrer
        </button>
      </div>
    </div>
  )
}
