'use client'

import { CalendarViewTypes, CalendarViewType } from '../../../utils/types'
import TimelineBlock from './TimelineBlock'

interface TimelineDataHourSegment {
  events: any[]
  startColIndex: number
  colCount: number
  baseZIndex: number
}

type TimelineDay = Record<string, TimelineDataHourSegment>

interface WeekViewData {
  days: Record<string, Record<string, TimelineDay>>
}

interface TimelineProps {
  calendarViewType: CalendarViewType
  data?: WeekViewData | any
  onClickTimelineBlock: (payload: any) => void
}

export default function Timeline({ calendarViewType, data = {}, onClickTimelineBlock,}: TimelineProps) {
  const weekViewTimeline = (weekData: WeekViewData) => {
    const week = Object.keys(weekData.days).map((dayKey, i) => {
      const dayHours = weekData.days[dayKey]
      const dayContent = Object.keys(dayHours).map((h, hourIdx) => {
        const minuteSegments = Object.keys(dayHours[h]).map(seg => {
          const {  events,  startColIndex, baseZIndex,} = dayHours[h][seg]
          return (
            <div
              key={`${dayKey}-${h}-${seg}`}
              style={{
                height: '11px',
                paddingLeft: `calc(2rem * ${startColIndex})`,
              }}
              className="relative flex w-full pr-2 timelineSegmentSpacing"
            >
              {events.map((e, idx) => (
                <TimelineBlock
                  key={idx}
                  index={idx}
                  baseZIndex={baseZIndex}
                  startColIndex={startColIndex}
                  event={e}
                  onClickTimelineBlock={onClickTimelineBlock}
                />
              ))}
            </div>
          )
        })

        return (
          <div
            key={`${dayKey}-${hourIdx}`}
            className="relative w-full h-12 border-b height-min"
          >
            {minuteSegments}
          </div>
        )
      })

      return (
        <div
          key={`day-${i}`}
          className="flex-1 w-1/7"
        >
          {dayContent}
        </div>
      )
    })
    return [...week.slice(1), week[0]]
  }

  const isWeekView = calendarViewType === CalendarViewTypes.WEEK_VIEW

  return (
    <div
      className={`
        flex-auto w-full h-full border-l
        ${isWeekView ? 'flex divide-x' : ''}
      `}
    >
      {isWeekView && weekViewTimeline(data as WeekViewData)}
    </div>
  )
}
