'use client'

import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import PanelLeft from './Panel/PanelLeft'
import CreateEventModal from '../Modal/CreateEventModal'
import TimelineWrapper from '../CalendarView/Timeline/TimelineWrapper'
import EventDetailModal from '../Modal/EventDetailModal'
import { CalendarViewTypes, CalendarViewType,} from '../../utils/types'
import { fetchEventsForCalendarType} from '../../reducers/calendar/calendarSlice'
import { setViewType, setSelectedEvent,} from '../../reducers/calendar/calendarSettingSlice'

interface SelectedEventState {
  eventUid: string
  top: number
  left: number
  height: number
  width: number
}

interface CalendarSettingState {
  isCreateEventModalOpen: boolean
  targetDate: string
  calendarViewType: CalendarViewType
  selectedEvent: SelectedEventState
}

interface RootState {
  calendarSetting: CalendarSettingState
  user: { userUid: string }
  db: {
    userDB: Record<string, any>
    eventDB: Record<string, any>
    userEventDB: Record<string, string[]>
    invitedDB: Record<string, any[]>
    eventGroupDB: Record<string, any>
     eventInviteDB: Record<string, any> 
  }
}

export default function GoogleAgenda() {
  const dispatch = useDispatch()
  const {  isCreateEventModalOpen,  targetDate,  calendarViewType, selectedEvent,} = useSelector((state: RootState) => state.calendarSetting)
  const user = useSelector((state: RootState) => state.user)
  const { userDB, eventDB, userEventDB, invitedDB, eventGroupDB, eventInviteDB } = useSelector((state: RootState) => state.db)
  const [isEditModalOpen, setIsEditModalOpen] = useState<boolean>(false)
  useEffect(() => {
    if (calendarViewType !== CalendarViewTypes.WEEK_VIEW) {
      dispatch(setViewType(CalendarViewTypes.WEEK_VIEW))
    }
  }, [calendarViewType, dispatch])
  useEffect(() => {
    if (targetDate && user.userUid) {
      dispatch(
        fetchEventsForCalendarType({
          userUid: user.userUid,
          calendarViewType: CalendarViewTypes.WEEK_VIEW,
          targetDate,
          db: {
            userDB,         
            userEventDB,
            eventDB,
            invitedDB,
            eventGroupDB,
            eventInviteDB, 
          },
        }),
      )
    }
  }, [ targetDate, user.userUid, userEventDB, eventDB, invitedDB, eventGroupDB, userDB, eventInviteDB, dispatch,])
  const onCloseEventDetailModal = () => {
    dispatch(
      setSelectedEvent({
        ...selectedEvent,
        eventUid: '',
      }),
    )
    setIsEditModalOpen(false)
  }

  return (
    <div className="flex flex-col w-full max-w-[1500px] mx-auto">
      <div className="flex gap-4">
        <PanelLeft />
        <div className="w-full flex flex-col">
          <TimelineWrapper />
        </div>
      </div>
      {isCreateEventModalOpen && <CreateEventModal />}
      {selectedEvent.eventUid.length > 0 &&
        selectedEvent.eventUid in eventDB && (
          <>
            <div className="absolute top-0 left-0 w-full h-full z-1" />
            <EventDetailModal
              selectedEvent={{
                ...selectedEvent,
                event: eventDB[selectedEvent.eventUid],
                eventCreator:
                  userDB[
                    eventDB[selectedEvent.eventUid].eventCreatorUid
                  ],
              }}
              onCloseModal={onCloseEventDetailModal}
            />
          </>
        )}
    </div>
  )
}
